.card {
  /*Acomoda uno a lado del otro*/
  display: flex;
  /*Le da el ancho a la card*/
  width: 300px;
  /*Primer parametro posiciona altura segundo parametro anchura*/
  margin: 10px 70px;
  /* border: 3px solid #000;*/
  /*Le da un border a la car encurbado*/
  border-radius: 20px;
  /*le da sombreado a la card*/
  box-shadow: 1px 1px 1px 1px rgb(0, 0, 0, 0.5);
}

img {
  width: 6rem;
  /*hace mas grande o peque√±a la card*/
  padding: 5px;
}
